<template>
  <div style="display: inline-block;" @mouseover="_showTip('hover')" @click="_showTip('click')" @mouseout="_closeTip">
    <slot></slot>
  </div>
  <div :class="wrapClasses" :style="wrapStyle">
    <div class="arrow"></div>
    <h3 class="popover-title">{{title}}</h3>
    <div class="popover-content">Vivamus sagittis lacus vel augue laoreet rutrum faucibus.</div>
    <!-- <div class="popover-content">{{content}}</div> -->
  </div>
</template>
<script>
  import {defaultProps, oneOf} from '../../views/utils/props'
  import cx from 'classnames'

  function getOffset (element) {
    let rect = element.getBoundingClientRect()
    let clientTop = element.clientTop
    let clientLeft = element.clientLeft
    return {
      top: rect.top - clientTop,
      left: rect.left - clientLeft
    }
  }

  export default {
    props: defaultProps({
      prefixCls: 'popover',
      title: String,
      content: String,
      trigger: oneOf(['hover', 'click'], 'hover'),
      placement: oneOf(['top', 'left', 'right', 'bottom'], 'top'),
      visible: false
    }),
    data () {
      return {
        wrapStyle: {}
      }
    },
    computed: {
      wrapClasses () {
        return cx({
          [this.prefixCls]: 1,
          [`fade in show`]: this.visible,
          [this.placement]: !!this.placement
        })
      }
    },
    methods: {
      _showTip (val) {
        var self = this
        console.log(this)
        self._getLocation(this)
        switch (val) {
          case 'hover':
            if (val === self.trigger) {
              self.visible = true
            }
            break
          case 'click': {
            if (val === self.trigger) {
              self.visible = !self.visible
            }
            break
          }
        }
      },
      _getLocation (els) {
        var self = this
        const elemOffset = getOffset(els.$el)
        console.log(self)
        switch (self.placement) {
          case 'top':
            self.wrapStyle = {
              top: elemOffset.top - els.$el.children[0].clientHeight + 'px',
              left: elemOffset.left + els.$el.clientWidth / 2 - els.$el.children[0].clientWidth / 2 + 'px'
            }
            break
          case 'left':
            self.wrapStyle = {
              top: elemOffset.top + els.$el.clientHeight / 2 - els.$el.children[0].clientHeight / 2 + 'px',
              left: elemOffset.left - els.$el.children[0].clientWidth + 'px'
            }
            break
          case 'right':
            self.wrapStyle = {
              top: elemOffset.top + els.$el.clientHeight / 2 - els.$el.children[0].clientHeight / 2 + 'px',
              left: elemOffset.left + els.$el.clientWidth + 'px'
            }
            break
          case 'bottom':
            self.wrapStyle = {
              top: elemOffset.top + els.$el.clientHeight + 'px',
              left: elemOffset.left + els.$el.clientWidth / 2 - els.$el.children[0].clientWidth / 2 + 'px'
            }
            break
        }
      },
      _closeTip () {
        // var self = this
        // if (self.trigger === 'hover') {
        //   self.visible = false
        // }
      }
    }
  }
</script>



